.block-background {
  border-radius: 10px;
  padding: 1rem 2rem;
}

.bordered-background {
  border: 1px dashed black;
  border-radius: 10px;
  padding: 0.5em;
  margin-top: 0.25em;
}

.block {
  width: 25%;
  padding: 0 1em;
  border-radius: 10px;
  position: relative;
}

.block label {
  margin-bottom: 0;
}

.block .form-group {
  margin-bottom: 0.5em;
}

#blockchain {
  max-width: 100%;
  overflow: auto;
  white-space: nowrap;
}
